html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

:root {
    --squareSize: 6vw;
    --pieceSize: 5vw;
}

.Square {
    display: inline-block;
    height: var(--squareSize);
    width: var(--squareSize);
    background-color: rgb(75, 153, 237);
    border: 5px groove #2480E7;
    margin-top: -3px;
    box-sizing: border-box;
    z-index: 0;
}

.InvisibleSquare {
    display: inline-block;
    height: var(--squareSize);
    width: var(--squareSize);
    padding-top: 5px;
    margin-top: 4px;
    opacity: 0;
}

.InvisibleSquare:hover {
    opacity: 1;
    cursor: pointer;
}

.Centered {
    display: block;
    margin: auto;
    width: 50%;
}

.RedPiece {
    height: var(--pieceSize);
    width: var(--pieceSize);
    background: rgb(242, 106, 96);
    border-radius: 50%;
    border: 5px solid #EC1605;
}

.RedPiecePlayed {
    height: var(--pieceSize);
    width: var(--pieceSize);
    background: rgb(242, 106, 96);
    border-radius: 50%;
    border: 5px solid #2480E7;
}

.YellowPiece {
    height: var(--pieceSize);
    width: var(--pieceSize);
    background: yellow;
    border-radius: 50%;
    border: 5px solid orange;
}

.YellowPiecePlayed {
    height: var(--pieceSize);
    width: var(--pieceSize);
    background: yellow;
    border-radius: 50%;
    border: 5px solid #2480E7;
}

.BlankCircle {
    height: var(--pieceSize);
    width: var(--pieceSize);
    background-color: white;
    border-radius: 50%;
    border: 5px solid #2480E7;
}


